.header .search {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: white;
  display: grid;
  place-items: center;
  border: 4px solid rgba(255, 255, 255, 0.3);
  background-clip: padding-box;
  cursor: pointer;
  transition: 0.5s;

  &:hover {
    border: 4px solid hsl(var(--color-primary) / 0.6);
    transition: 0.5s;
  }
}
